import classnames from 'classnames';
import { send } from 'process';
import QRCode from "qrcode.react";
import React, { useEffect, useState } from 'react';
import {
    BrowserView, isBrowser,
    MobileView
} from "react-device-detect";
import { atom, useRecoilState, useRecoilValue, useSetRecoilState } from 'recoil';
import data, { productTable } from '../core/data';
import Days from '../core/Days';
import fbase from '../fb/fbase';

const paymentStepState = atom({
    key: "paymentStepState",
    default: 0,
})
const selectPlanState = atom({
    key: "selectPlanState",
    default: '',
})

const sendTelemessage = ({
    sendTitle,
    email,
    uid,
    priceCode,
    moreStr,
}: {
    sendTitle: string,
    email: string
    uid: string
    priceCode: string,
    moreStr: string,
}) => {
    data.sendTelelog(`${sendTitle}::${email}##${uid}##${priceCode}##${moreStr}`)
}
const AlertBuyProduct2 = ({
    openParam
}: {
    openParam: any
}) => {

    const [paymentStep, setpaymentStep] = useRecoilState(paymentStepState);

    useEffect(() => {
        setpaymentStep(0);
    }, [])

    return (
        <div className="AlertBuyProduct2">
            {paymentStep === 0 && <PayStep01 />}
            {paymentStep === 1 && <PayStep02 />}
        </div>
    );
};

const PayStep01 = () => {
    const setPaymentStep = useSetRecoilState(paymentStepState);
    const [selectPlan, setSelectPlan] = useRecoilState(selectPlanState);
    return (<div className="PayStep01">
        <div className="header">
            <div className="title">
                 
                </div>
            <button className="btn btn-close" onClick={() => {

                data.hideAlert();
            }}><i className="fas fa-times"></i></button>
        </div>
        <div className="notice">
            .    .<br></br>
                      <br></br>   . <br></br>
                    <a href="mailto:dev@casete.in"></a>  <a href="http://pf.kakao.com/_HxlMJK/chat" target="_blank"></a> . <br></br>

        </div>
        <ul className="plan-list">
            <li className={classnames(["btn-plan-radio", { active: selectPlan === '1m' }])} onClick={() => {
                setSelectPlan('1m');
            }}>
                <div className="bullet"><i className="fas fa-check"></i></div>
                <div>1 <div className="price">{data.numberWithCommas(productTable['1m'].price)}<span className="per-month">(=2,400/)</span></div></div>

            </li>
            <li className={classnames(["btn-plan-radio", { active: selectPlan === '3m' }])} onClick={() => {
                setSelectPlan('3m');
            }}>
                <div className="bullet"><i className="fas fa-check"></i></div>
                <div>3 <div className="price">{data.numberWithCommas(productTable['3m'].price)}<span className="per-month">(=2,000/)</span></div></div>

            </li>
            <li className={classnames(["btn-plan-radio", { active: selectPlan === '6m' }])} onClick={() => {
                setSelectPlan('6m');
            }}>
                <div className="bullet"><i className="fas fa-check"></i></div>
                <div>6 <div className="price">{data.numberWithCommas(productTable['6m'].price)}<span className="per-month">(=1,500/)</span></div></div>

            </li>
            <li className={classnames(["btn-plan-radio", { active: selectPlan === '12m' }])} onClick={() => {
                setSelectPlan('12m');
            }}>
                <div className="bullet"><i className="fas fa-check"></i></div>
                <div>12 <div className="price">{data.numberWithCommas(productTable['12m'].price)}<span className="per-month">(=1,200/)</span></div></div>
            </li>
        </ul>
        <div className="footer">
            <button className={classnames(["btn-confirm", { active: selectPlan !== '' }])} onClick={() => {
                if (selectPlan === '') {
                    alert('  .');
                    return;
                }
                // console.log('fbase.userMoreInfo', fbase.userMoreInfo)
                sendTelemessage({
                    sendTitle: '',
                    email: fbase.userMoreInfo.email,
                    uid: fbase.userMoreInfo.uid,
                    priceCode: selectPlan,
                    moreStr: '',
                });
                setPaymentStep(1)
            }}></button>
        </div>

    </div>);
}

const PayStep02 = () => {
    const [buyStep, setBuyStep] = useState(0);
    const [stepCD, setStepCD] = useState(isBrowser ? 1 : 0);
    const [selectPlan, setSelectPlan] = useRecoilState(selectPlanState);
    if (isBrowser) {
        //   30 
        setTimeout(() => {
            setStepCD(2);
        }, 15 * 1000);
    }
    const qrurl = 'https://qr.kakaopay.com/281006012180682200001379' + productTable[selectPlan].priceQR;
    return (<div className="PayStep02">
        <div className="header">
            <div className="title">
                <img className="kakaopay-logo" src="https://img.etnews.com/photonews/2009/1337259_20200915103744_396_0001.jpg"></img> 
                </div>
            <button className="btn btn-close" onClick={() => {
                data.hideAlert();
            }}><i className="fas fa-times"></i></button>
        </div>

        <div className="message">
            {productTable[selectPlan].title} ( {data.numberWithCommas(productTable[selectPlan].price)} )
            </div>

        <div className="step step1">

            <div className="step1-in">
                <BrowserView>
                    <ul>
                        <li className="title">
                            <div className="message-no">
                                <div className="bullet-no">1</div>
                                <div>   QR    ,</div>
                            </div>

                            <div className="small">
                                <div>    </div>
                                <div> ({fbase.userMoreInfo.email})   .</div>
                            </div>

                        </li>
                        <li className="qr-container">
                            {
                                stepCD > 0
                                    ?
                                    <QRCode value={qrurl} />
                                    :
                                    <div className="btn-create-qr" onClick={() => {
                                        setStepCD(1);

                                    }}>
                                        <div className="cover">
                                            <i className="fas fa-redo-alt"></i>
                                            <div className="desc"></div>
                                        </div>
                                        <div className="dummy"><QRCode value="qr  " /></div>
                                    </div>
                            }

                        </li>
                        <li className="title">
                            <div className="message-no">
                                <div className="bullet-no">2</div>
                                <div>    .</div>
                            </div>

                        </li>
                    </ul>

                </BrowserView>
                <MobileView>
                    <ul>
                        <li className="title">
                            <div className="bullet-no">1</div>
                                  (macaoshu@gmail.com)   .
                        </li>
                        <li className="qr-container">
                            <a onClick={() => {
                                setTimeout(() => {
                                    setStepCD(2);
                                }, 15 * 1000);
                            }} className="btn-gotokakao" href={qrurl} target="_blank"> <i className="fas fa-angle-right"></i></a>
                        </li>
                        <li className="title"><div className="bullet-no">2</div>     .</li>
                    </ul>

                </MobileView>
                {/* <div>       .</div> */}
            </div>
        </div>

        <div className="step step2">
            <div className="notice">
                  <a href="mailto:dev@casete.in"></a>  <a href="http://pf.kakao.com/_HxlMJK/chat" target="_blank"></a>  .
                </div>
            <button className={classnames(["btn-confirm", `stepCD-${stepCD}`])} onClick={() => {
                if (stepCD < 2) {
                    alert('    .');
                    return;
                }
                alert(' (   15  )  >    . ')
                const monNum = ({
                    "1m": 1,
                    "3m": 3,
                    "6m": 6,
                    "12m": 12
                } as any)[selectPlan];
                const todayStr = Days.utc().local().format('YYYYMMDD');
                const enddayStr = Days(todayStr).add(monNum, 'month').format('YYYYMMDD')
                const fbasesubscriptionInfo = `t${selectPlan}##${todayStr}##${enddayStr}`;
                fbase.userMoreInfo.subscriptionInfo = fbasesubscriptionInfo;
                sendTelemessage({
                    sendTitle: '',
                    email: fbase.userMoreInfo.email,
                    uid: fbase.userMoreInfo.uid,
                    priceCode: selectPlan,
                    moreStr: fbasesubscriptionInfo,
                });
                fbase.syncMoreInfo();
                setTimeout(() => {
                    data.hideAlert();
                    window.location.reload();
                }, 1000);
            }}></button>
        </div>

    </div>);
}

// export default AlertBasic;
export default AlertBuyProduct2;