import classnames from 'classnames';
import { css } from 'emotion';
import React from 'react';
import data from '../core/data';
import fbase from '../fb/fbase';
import {
    BrowserView, isBrowser,
    MobileView
} from "react-device-detect";

let uniqCnt = 0;
const PageHome = () => {
    // data.topicMainJSON = topicMainJSON;
    const course1 = data.mainJSON.map((item1: any, i: number) => {
        if (item1.isHomeList === "true") {
            return <CourseItem json={item1} key={uniqCnt++} />
        }

    });
    return (
        <div className="PageHome">
            <div className="notice">
                <BrowserView>
                    <div className="tit-main">
                        <div className="pc-br">     </div>
                        <div className="pc-br"><span className="em">  </span> !</div>
                    </div>
                    <div className="tit-sub">
                        <div className="pc-br">      .</div>
                        <div className="pc-br">       .</div>
                    </div>
                </BrowserView>
                <MobileView>
                    <div className="tit-main">
                             <span className="em">  </span> !
                    </div>
                    <div className="tit-sub">
                              .        .
                    </div>
                </MobileView>

                <button onClick={() => {
                    data.gotoPage('CourseMain', 'fOICLMmU2FsdGVkX1/4I7rfm74h2kmq+Q4IVv7MCO6OQUOvV+WSk/YThnQu8EQAqMped1ANlDHsQuCZHUjXi/3kfRTvqg==');
                }} className="btn btn-start-free">   <i className="fas fa-long-arrow-alt-right"></i></button>
            </div>

            <ul className="course-list">
                {course1}
            </ul>
        </div>
    );
};

const CourseItem = ({
    json
}: {
    json: any
}) => {
    const isOwner = !['needLogin', 'needPurchase'].includes(fbase.isSubscriptionInfo());
    // const isOwner: boolean = true;

    return (
        <>

            <li className="course-item" onClick={() => {
                data.gotoPage('CourseMain', json.query);
            }}>
                <div className={classnames(["cover-img", css`
                background-image: url("${data.getRootPath()}resource/courses/${json.courseID}/cover.png");
            `])}>
                    <div className={classnames(["tag-iscomplete", { ing: json.isMakeComplete === "false" }])}>
                        {json.isMakeComplete === "false" ? "" : ""}
                    </div>
                </div>
                <div className="info">
                    <h3>{json.title}</h3>
                    <div className="info-sub">
                        <div className="course-num">{json.item.length} </div>
                        <Tags str={json.tags} key={json.courseID} />
                        {/* <div className="btns-func">
                            {data.numberWithCommas(parseInt(json.prices[0]))}
                            {
                                isOwner && <span></span>
                            }
                        </div> */}

                    </div>


                </div>
            </li>
        </>

    )
}

export const Tags = ({
    str
}: {
    str: string
}) => {
    const arr = str.split(',');
    let Tag = arr.map((item: string, i: number) => {
        return <span className="tag" key={i}>#{item}</span>
    });
    if (str === '') {
        Tag = [];
    }
    return (
        <div className="Tags">
            {Tag}
        </div>
    );
}

export default PageHome;